<template>
    <v-form>
           <v-text-field
           :rules="rules.nameRules"
            v-model="numberFisrt"
            label="Ultimo valor medido na conta de luz"
            required
            v-mask="'####'"
            ></v-text-field>

            <v-text-field
                :rules="rules.nameRules"
                v-model="numberLast"
                label="Valor atual no seu relogio"
                required
                  v-mask="'####'"
            ></v-text-field>

            <v-text-field
                :rules="rules.nameRules"
                v-model="kwh_atual"
                label="Valor atual do kwh na sua cidade"
                required
                 v-mask="'#.##'"
            ></v-text-field>

            <v-text-field
                v-model="result"
                label="Valor da sua conta"
                required
            ></v-text-field>

            <v-text-field
                v-model="kwh_consumo"
                label="consumo"
                required
            ></v-text-field>

   
           <v-btn
           
            @click="calcularConta"
              x-large
              dark
                color="primary"
            >
             Calular
    </v-btn>

    </v-form>
</template>


<script>
import {mask} from 'vue-the-mask'
// import Vuetify from 'vuetify'
export default {
    name: 'Form',
    data(){
        return{
            numberFisrt :null,
            numberLast : null,
            kwh_atual:null,
            kwh_consumo:null,
            result:null,

            rules:{
                nameRules: [
                    v => !!v || 'Esse campo deve ser preenchido',
                ],
            }
        }
    },
    directives: {mask},

    methods:{   
        calcularConta:function(){
            this.numberLast = this.numberLast.replace('.',',')

            let result = this.numberLast - this.numberFisrt
            this.kwh_consumo =this.numberLast - this.numberFisrt
            result = result * this.kwh_atual
            this.result = 'R$ ' +result.toFixed(2).replace('.',',') 

            localStorage.setItem('ultimoValor',this.numberFisrt)
        },
    },  
    mounted: function () {
        this.$nextTick(function () {
             if (localStorage.getItem('ultimoValor')!= null){
                let number = localStorage.getItem('ultimoValor')
                number = parseInt(number)
                console.log(number)
                this.numberFisrt = number    
            }
        })
    },
}


</script>


<style scoped>
    form{
        text-align: left;
        max-width: 385px;
        width: 100%;
        margin: 0 10px;
        border: 1px solid #ccc;
        border-radius: 16px;
        padding: 20px;
    }
    .v-text-field__slot {
        padding: 9px 20px 0px 20px;
    }

    .v-btn{
        background: red;
    }
   
</style>
